.header {
    width: 100%;
    height: 50px;
    z-index: 1;
    position: fixed;
    background-color: beige;
    border-bottom: 1px solid rgb(218, 220, 224);
    box-shadow: rgba(0, 0, 0, 0.6) 0px 4px 4px;
    display: flex;
    align-items: center;

}

.logo{
    flex-grow: 1;
    color: #a01f29;
    margin-left: 10px;
}
.header nav{
    margin-right: 10px;
}


.header nav button{
    height: 50px;
    border: 0px;
    cursor: pointer;
    background-color: beige;
    outline: none;
}

.header nav button:hover{
    background-color: rgb(218, 220, 224);
}